#country-details {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;

  h2 {
    font-size: 5rem;
  }

  div {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 1rem;

    &.last-child {
      margin-bottom: 0;
    }

    .label {
      display: block;
      font-size: 1.5rem;
      color: #999;
    }
  }
}
